import React from 'react';
import {Button, Card, Modal, PageHeader, Typography} from 'antd';
import HomePage from "./component/HomePage";
import AboutUs from "./component/AboutUs";
import ContactUs from "./component/ContactUs";
import {ContactsOutlined, InfoCircleOutlined} from "@ant-design/icons";

const App = () => {
    const modal = (content) => {
        return Modal.success({
            icon: null,
            content: content,
            maskClosable: true,
            destroyOnClose: true,
            centered: false
        });
    }

    return (
        <Card bordered={false}>
            <PageHeader
                title={<Typography.Title style={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                }} code>Random Code OSS</Typography.Title>}
                extra={[<Button icon={<InfoCircleOutlined />} type="text" onClick={() => {
                    modal(<AboutUs/>)
                }}>About Us</Button>,
                    <Button icon={<ContactsOutlined />} type="text" onClick={() => {
                        modal(<ContactUs/>)
                    }}>Contact Us</Button>]}
            />
            <HomePage/>
            <h6 style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                position: "fixed", left: 0,
                bottom: 0,
                right: 0
            }}>
                Copyright Â© 2021 Random Code OSS - All Rights Reserved.
            </h6>
        </Card>

    );
}


export default App;